<template>
  <div>
    <div>I am in Home page</div>
    <div class="flexBox">
      <span>ref:{{ count }}</span>
      <span>reactive:{{ reactiveField }}</span>
      <span>{{ objA }}</span>
    </div>
    <div class="flexBox">
      <button @click="addRef">addRef</button>
      <button @click="addReactive">addReactive</button>
      <button @click="addobjA">addobjA</button>
    </div>
  </div>
</template>

<script>
//ref valueref 
//  string  number   vuedefineProperty proxy  ref(3) data(2)
// reactive   reactive Proxyset forceUpdate
import { ref, reactive, toRefs } from 'vue'
export default {
  setup() {
    const count = ref(0)
    const state = reactive({
      reactiveField: 0,
      objA: ['a', 'b', 'c'],
    })
    const addRef = () => {
      count.value++
    }
    const addReactive = () => {
      state.reactiveField++
    }
    const addobjA = () => {
      state.objA[0] = 'x'
    }
    return {
      count,
      ...toRefs(state),
      addRef,
      addReactive,
      addobjA,
    }
  },
}
</script>

<style scoped>
.flexBox {
  width: 600px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>
